 background-color: #20232a;\n    opacity: 0.8;\n  }\n\n  .m-modal-content {\n    position: relative;\n    z-index: 10002;\n    box-sizing: content-box;\n    text-align: center;\n    min-width: 200px;\n    max-width: 240px;\n    background-color: #fff;\n    opacity: 0.95;\n    border-radius: $default-border-radius;\n    box-shadow: 0 3px 20px rgba(32, 35, 42, 0.5);\n    color: $default-color;\n    display: flex;\n    flex-direction: column;\n    overflow: auto;\n    padding: 24px;\n    transition: 0.5s box-shadow;\n\n    &:hover,\n    &:focus,\n    &:active {\n      opacity: 1;\n    }\n\n    .m-title {\n      margin-bottom: 8px;\n      overflow: hidden;\n      white-space: nowrap;\n      text-overflow: ellipsis;\n      line-height: 1.2em;\n    }\n\n    .m-details {\n      margin-bottom: 10px;\n      p {\n        font-size: 12px;\n        margin-top: 3px;\n        margin-bottom: 3px;\n      }\n    }\n\n    a {\n      cursor: pointer;\n      padding: 10px;\n      color: $default-color;\n      border-radius: $default-border-radius;\n      text-decoration: none;\n\n      &.m-gmail {\n        color: $gmail-color;\n        &:hover,\n        &:focus,\n        &:active {\n          background-color: rgba(212, 70, 56, 0.08);\n          color: $gmail-color;\n        }\n      }\n\n      &.m-outlook {\n        color: $outlook-color;\n        &:hover,\n        &:focus,\n        &:active {\n          background-color: rgba(0, 114, 198, 0.08);\n          color: $outlook-color;\n        }\n      }\n\n      &.m-tg {\n        color: $telegram-color;\n        &:hover,\n        &:focus,\n        &:active {\n          background-color: rgba(0, 136, 204, 0.08);\n          color: $telegram-color;\n        }\n      }\n\n      &.m-wa {\n        color: $wa-color;\n        &:hover,\n        &:focus,\n        &:active {\n          background-color: rgba(0, 191, 165, 0.08);\n          color: $wa-color;\n        }\n      }\n\n      &.m-skype {\n        color: $skype-color;\n        &:hover,\n    