e {\n            background-color: rgba(0, 191, 165, 0.08);\n            color: $wa-dark-color;\n          }\n        }\n\n        &.m-skype {\n          color: $skype-dark-color;\n\n          &:hover,\n          &:focus,\n          &:active {\n            background-color: rgba(0, 175, 240, 0.08);\n            color: $skype-dark-color;\n          }\n        }\n\n        &.m-by {\n          &:hover,\n          &:focus,\n          &:active {\n            color: #fff;\n          }\n        }\n      }\n    }\n  }\n}\n"]}]);
